import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    // index
    fetchResidencias(ctx, { queryParams }) {
      return new Promise((resolve, reject) => {
        axios
          .get('/residencias', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // show
    fetchResidencia(ctx, { id }) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/residencias/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // store
    addResidencia(ctx, residenciaData) {
      return new Promise((resolve, reject) => {
        axios
          .post('/residencias', residenciaData)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // update
    updatedResidencia(ctx, residenciaData) {
      return new Promise((resolve, reject) => {
        axios
          .put(`/residencias/${residenciaData.id}`, residenciaData)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    // delete
    deleteResidencia(ctx, residenciaData) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/residencias/${residenciaData.id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
