<template>
  <!-- modal login-->
  <b-modal
    id="modal-residencia-form"
    ref="refModalResidenciaForm"
    v-model="dialog"
    :title="formTitle"
    size="md"
    no-close-on-backdrop
    :hide-footer="true"
    no-close-on-esc
    hide-header-close
  >
    <b-form @submit.prevent="onSubmit">

      <b-row>
        <b-col cols="12">
          <b-form-group
            label="Nombre"
            label-for="hi-first-name"
            label-cols-md="4"
          >
            <b-input-group class="input-group-merge">
              <b-input-group-prepend is-text>
                <feather-icon icon="HomeIcon" />
              </b-input-group-prepend>
              <b-form-input
                id="hi-first-name"
                v-model="residenciaData.nombre"
                placeholder="Nombre"
              />
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col cols="12">
          <b-form-group
            label="Código"
            label-for="hi-email"

            label-cols-md="4"
          >
            <b-input-group class="input-group-merge">
              <b-input-group-prepend is-text>
                <feather-icon icon="CodeIcon" />
              </b-input-group-prepend>
              <b-form-input
                id="hi-email"
                v-model="residenciaData.codigo"
                type="text"
                placeholder="Código"
              />
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col cols="12">
          <b-form-group
            label="Aforo"
            label-for="hi-number"
            label-cols-md="4"
          >
            <b-input-group class="input-group-merge">
              <b-input-group-prepend is-text>
                <feather-icon icon="ApertureIcon" />
              </b-input-group-prepend>
              <b-form-input
                id="hi-number"
                v-model="residenciaData.aforo"
                type="number"
                placeholder="Aforo"
              />
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col cols="12">
          <b-form-group
            label="Descripción"
            label-for="hi-password"
            label-cols-md="4"
          >
            <b-form-textarea
              v-model="residenciaData.descripcion"
              placeholder="Descripcion..."
              rows="3"
              max-rows="6"
            />
          </b-form-group>
        </b-col>

        <!-- submit and reset -->
        <b-col offset-md="4">
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            type="submit"
            variant="primary"
            class="mr-1"
          >
            Guardar
          </b-button>
          <b-button
            v-ripple.400="'rgba(186, 191, 199, 0.15)'"
            type="reset"
            variant="outline-secondary"
            @click="close"
          >
            Cerrar
          </b-button>
        </b-col>
      </b-row>

    </b-form>
  </b-modal>

</template>

<script>
import {
  BRow, BCol, BForm, BModal, BFormGroup, BFormTextarea, BButton, BFormInput, BInputGroup, BInputGroupPrepend,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import {
  ref, watch, toRefs, nextTick, computed,
} from '@vue/composition-api'
import store from '@/store'
// Notification
import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
  components: {
    BRow,
    BCol,
    BModal,
    BForm,
    BFormGroup,
    BFormTextarea,
    BButton,
    BFormInput,
    BInputGroup,
    BInputGroupPrepend,
  },
  directives: {
    Ripple,
  },
  props: {
    persona: {
      type: Object,
      default() {
        return { }
      },
    },
    editedItem: {
      type: Object,
      default() {
        return { }
      },
    },
  },
  setup(props, { emit }) {
    const { editedItem } = toRefs(props)
    const editedIndex = ref(-1)
    const dialog = ref(false)
    const toast = useToast()

    const blankResidenciaData = {
      id: 1,
      nombre: '',
      codigo: '',
      aforo: null,
      descripcion: null,
      estado: 'activo',
    }

    const residenciaData = ref(JSON.parse(JSON.stringify(blankResidenciaData)))

    const resetuserData = () => {
      residenciaData.value = JSON.parse(JSON.stringify(blankResidenciaData))
    }

    const close = () => {
      dialog.value = false
      nextTick(() => {
        resetuserData()
        editedIndex.value = -1
      })
    }

    const formTitle = computed(() => (editedIndex.value === -1 ? 'Nuevo' : `Editar N° ${editedIndex.value}`))

    watch(dialog, () => {
      // eslint-disable-next-line no-unused-expressions
      dialog.value || close()
    })

    // watch(persona, () => {
    //   resetuserData()
    //   residenciaData.value.usuario_id = persona.value.usuario_id
    //   residenciaData.value.persona = persona.value
    //   editedIndex.value = -1
    // })

    watch(editedItem, () => {
      console.log('vemos para editar::::::::::', editedItem)
      editedIndex.value = editedItem.value.id
      // Datos de triaje
      residenciaData.value = editedItem.value
      // residenciaData.value.persona = editedItem.value.persona_usuario
      // residenciaData.value.servidor_publico_id = editedItem.value.servidor_publico_id
      // residenciaData.value.usuario_id = editedItem.value.usuario_id
    })

    const onSubmit = () => {
      if (editedIndex.value > -1) {
      // vamos a editar
        console.log('Vamos a editar')
        store.dispatch('residencias/updatedResidencia', residenciaData.value)
          .then(() => {
          // Clean form, Close Modal y Refresh Table
            toast({
              component: ToastificationContent,
              props: {
                title: '¡Actualización exitosa',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
            dialog.value = false
            emit('refresh-data-residencias-table')
          })
          .catch(error => {
            console.log(error)
          })
      } else {
        console.log('Vamos a un nuevo registro', residenciaData.value)
        store.dispatch('residencias/addResidencia', residenciaData.value)
          .then(() => {
          // Clean form, Close Modal y Refresh Table
            toast({
              component: ToastificationContent,
              props: {
                title: '¡Registro exitoso!',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
            dialog.value = false
            emit('refresh-data-residencias-table')
          })
      }
    }

    return {
      residenciaData,
      resetuserData,
      onSubmit,
      close,
      dialog,
      formTitle,
    }
  },

}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-flatpicker.scss';
@import '@core/scss/vue/libs/vue-select.scss';

  /* Number Type Input Box Scss for - Remove arrow on hover */
  input[type='number']::-webkit-inner-spin-button,
  input[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: auto !important;
  }
</style>
